import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import Zoom from 'react-reveal/Zoom';
// import Pulse from 'react-reveal/Pulse';

import '../css/popup.css';
import '../css/responsive.css';

import { Popover, Image } from 'react-bootstrap';
import { selectDishType, resetInput } from '../actions/index';

import Icon from './Icon';

const mapDispatchToProps = dispatch => {
  return {
    selectDishType: index => dispatch(selectDishType(index)),
    resetInput: () => dispatch(resetInput())
  };
};

// constants

const numOfMeals = 3;

var content = [{
  title: "Try Some Organic Pancake",
  text: "Too busy to make food everyday but still want to eat healthily? This organic pancake is the perfect choice!",
  imgInd: 1
}, {
  title: "Try Some Organic Salad",
  text: "The most healthy choice for you!",
  imgInd: 2
}, {
  title: "Try Some Organic Sandwich",
  text: "Try a organic sandwich for a healthy change!",
  imgInd: 3
}];

// const contentList = index => {
//   if (index < 1 || index >= 4) return;
//   return content[index-1];
// }
//
// const popupMeal = index => {
//   if (index < 1 || index >= 4) return;
//   return  <Image className="popup-img" src={require("../img/popup-meal-" + index + ".jpg")} circle />
// }

class Popup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      class: "hidden",
      show: false,
      time: 5000,
      index: -1,
      icon: false
      // display: props.display
    }
    this.handleClick = this.handleClick.bind(this);
  }

  show() {
    this.setState({class : 'popup', show: !this.state.show});
  }

  componentDidUpdate(prevProps, prevState) {
    // if (prevState.display === false) {
    //   if (prevState.display !== this.state.display) {
    //     this.show();
    //     this._interval = setInterval(() => {
    //
    //       this.setState({show: !this.state.show});
    //       // this._internalTimer = setTimeout(() => this.setState({show: true}), this.props.delay);
    //     }, (this.props.wait));
    //
    //     // this._stopInterval = setTimeout(() => {
    //     //
    //     //   this.setState({show: false});
    //     //   clearTimeout(this._internalTimer);
    //     //   clearInterval(this._interval);
    //     // }, this.props.wait * this.props.num + this.props.delay);
    //   }
    // }
  }

  static getDerivedStateFromProps(props, state) {
    // if (state.display === true) {
    //   if (props.display !== state.display) {
    //     return {
    //       class: 'hidden',
    //       show: false,
    //       display: props.display
    //     };
    //   }
    // }
    //
    // if (props.display !== state.display) {
    //   return {
    //     display: props.display
    //   };
    // }

    // Return null to indicate no change to state.
    return null;
  }

  componentWillUnmount() {
    // clearTimeout(this._stopInterval);
    clearTimeout(this._iconTimeout);
    clearInterval(this._interval);
  }

  componentDidMount() {
    this._interval = setInterval(() => {
      this.state.index < 0 ? this.setState({ index: this.state.index+1 }):
        this.setState({
          show: !this.state.show,
          index: this.state.show ? this.state.index :
                                  (this.state.index >= 2 ? 0:this.state.index+1),
        })
    }, this.state.time);

    this._iconTimeout = setTimeout(() => this.setState({ icon: true }), this.state.time*numOfMeals*2)


  }

  hide() {
    this.setState({class: 'hidden'});
  }

  handleClick(i) {
    this.props.selectDishType(i);
    this.props.resetInput();
  }

//         <div className={this.state.class}>
// <div className='popup_inner'>
// </div>

  render() {
    const { index, icon } = this.state;
    // console.log(typeof(content[0].img) + ", " + content[0].img);
    // const img = content[index].img;
    return (
      <div>
        <div className="popup">
          <Zoom cascade when={this.state.show}>
            <div className="popup_inner">
              <Popover
                id="popover-basic"
                className="popover"
                placement="top"
                title={
                  index >= 0 && index < 3 ?
                  content[index].title : ""
                }
              >
                {
                  index >= 0 && index < 3 ?
                  content[index].text : ""
                }
              </Popover>
              <Link
                to={'/selected'}
                className="popup-content"
                onClick={() => this.handleClick()}
              >
              {

                index >= 0 && index < 3 ?
                <Image
                  className="popup-img"
                  src={require("../img/popup-meal-" + content[index].imgInd + ".jpg")} circle />
                 : ""
              }
              </Link>
            </div>

          </Zoom>
        </div>
        <Icon
          index = {index}
          show = {icon}
        />
      </div>
    )
  }
}

export default withRouter(connect(null, mapDispatchToProps)(Popup));



